<template>
  <div>
    <el-container>
      <!--侧边栏-->
      <el-aside width="200px">
        <el-menu :default-openeds="['1']">
          <el-submenu index="1">
            <template slot="title"><i class="el-icon-caret-right"></i>用户管理</template>
            <el-menu-item-group>
              <el-menu-item index="1-1">
                <!--<router-link to="/user/profile">个人信息</router-link>-->
                <!--name:传组件名，params：传递参数，需要对象：v-bind-->
                <router-link v-bind:to="{name: 'UserProfile',params: {id: 1,name: 'justice'}}">个人信息</router-link>
              </el-menu-item>
              <el-menu-item index="1-2">
                <router-link to="/user/list">用户列表</router-link>
              </el-menu-item>
              <el-menu-item index="1-3">
                <router-link to="/goHome">回到首页</router-link>
              </el-menu-item>
            </el-menu-item-group>
          </el-submenu>
          <el-submenu index="2">
            <template slot="title"><i class="el-icon-caret-right"></i>内容管理</template>
            <el-menu-item-group>
              <el-menu-item index="2-1">分类管理</el-menu-item>
              <el-menu-item index="2-2">内容列表</el-menu-item>
            </el-menu-item-group>
          </el-submenu>
          <el-submenu index="3">
            <template slot="title"><i class="el-icon-caret-right"></i>系统管理</template>
            <el-menu-item-group>
              <el-menu-item index="3-1">用户设置</el-menu-item>
            </el-menu-item-group>
          </el-submenu>
        </el-menu>
      </el-aside>

      <!--上下-->
      <el-container>
        <!--顶部栏-->
        <el-header style="text-align: right; font-size: 12px">
          <el-dropdown>
            <i class="el-icon-setting" style="margin-right: 15px"></i>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item>个人信息</el-dropdown-item>
              <el-dropdown-item @click.native="logout">退出登录</el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
          <!--前端传来的名字-->
          <span>{{ this.$store.getters.getUser.name }}</span>
        </el-header>

        <!--主页信息-->
        <el-main>
          <!--点击router-link展示router-view,及展示的位置-->
          <router-view/>
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>

<script>
export default {
  // props: ['name'],
  name: "Main",
  methods: {
    logout() {
      window.sessionStorage.clear();
      this.$router.push('/login')
    }
    // logout: function () {
    //   alert('zyyyds')
    // }
  }
}
</script>


<style scoped lang="scss">
.el-header {
  background-color: #a9c1ea;
  color: #333;
  line-height: 60px;
}

.el-aside {
  color: #333;
}
</style>



